import { mapState } from "vuex";

export default {
  scrollToTop: true,
  data: () => ({
    header: {
      title: "WATORA",
    },
    arch: null,
    error: null,
    isLoading: false,
    itemActive: null,
    isAosOnce: true,
  }),

  watch: {
    itemActive(newVal, oldVal) {
      if (newVal) {
        this.$store.commit("live2dText", `要阅读『${newVal} 』吗?`);
      }
    } /*
    $route: {
      handler(to, from) {
        if (process.client) {
          this.$nextTick(() => {
            setTimeout(() => {
              document
                .getElementById("container")
                .scrollIntoView({ behavior: "smooth" });
            }, 0);
          });
        }
      },
      deep: true,
      immediate: true,
    },*/,
  },
  computed: {
    ...mapState(["page", "scroll", "archives", "labels", "categorys"]),
    isMobile() {
      return this.$store.getters.isMobile;
    },
    res() {
      const icon = `${this.$static}/icon/icon.png`;
      const placeholder = this.$src.images.placeholder;
      return {
        icon,
        placeholder,
      };
    },
  },

  methods: {
    async onChange(page) {
      this.$router.push({ params: { page } });
    },
    bannerHeight() {
      if (process.client) {
        const banner = document.getElementById("banner");
        if (banner) {
          return banner.offsetHeight;
        }
        return NaN;
      }
    },
  },
  async fetch({ store, params }) {
    await Promise.all([
      store.dispatch("archives", {
        page: Number(params.page || 1),
        count: 10,
      }),
      store.dispatch("labels"),
      store.dispatch("categorys"),
    ]);
  },
};
